.main_section2 {
    position: inherit;
    height: 70vh;
    background-color: var(--white);
}
.img_container{
    height: 100%;
    width: 100%;
    background-color: var(--primary_color);
    display: flex;
    align-items: center;
}
.img_container img{
    height: 100%;
    width: 100%;
    background-size: cover;
    opacity: 90% ;
}
.img_container .txt_container{
    width: 42%;
    position: absolute;
    right: 7%;
}
.img_container .txt_container h2{
    font-size: 5rem;
    color: var(--primary_color);
}
.img_container .txt_container h3{
    font-size: 2rem;
    margin-top: 8px;
    color: var(--solid_white);
}
.img_container .txt_container h4{
    font-size: 2rem;
    margin-top: 10px;
    color: var(--primary_color);
}
.img_container .txt_container p{
    font-size: 1.3rem;

    margin-top: 20px;
    font-weight: 200;
    color: var(--solid_white);
}