.main_section1 {
    position: inherit;
    padding: 100px;
    height: auto;
    background-color: var(--white);
}

.txt_content {
    text-align: center;
    margin: 0 auto;
}

.txt_content h2 {
    color: var(--primary_color);
    font-size: 2.5rem;
}

.txt_content p {
    color: var(--black);
    margin-top: 20px;
    margin-bottom: 50px;
    font-weight: 200;
}

.cards {
    max-width: 1400px;
    height: auto;
    margin: 0 auto;
    z-index: 10;
}

.cards .card_1 {
    display: flex;
    padding: 30px;
    background: var(--bg_cards_color);
    border: 1px solid var(--primary_color);
    border-radius: var(--border_radius);
    transition: .5s;
}
.cards .card_1:hover{
    background: var(--card_hover);

}

.cards .card_1 img {
    max-width: 800px;
    height: 400px;
}

.cards .card_1 .content_card_1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.cards .card_1 .content_card_1 .title {
    height: auto;
    display: flex;
    align-items: center;
}

.cards .card_1 .content_card_1 .title svg {
    font-size: 50px;
    margin-right: 20px;
}

.cards .card_1 .content_card_1 .title h2 {
    font-size: 3rem;
    color: var(--black);
}

.cards .card_1 .content_card_1 h3 {
    font-weight: 300;
    font-size: 1.5rem;
    padding: 12px 0;
    color: var(--black);
}

.cards .card_1 .content_card_1 p {
    font-weight: 100;
    color: var(--black);
}

.cards .card_1 .content_card_1 .btn_view {
    width: 140px;
    margin-top: 50px;
    padding: 10px 25px;
    color: var(--black);
    text-decoration: none;
    text-align: center;
    transition: .3s;
}

.cards .card_1 .content_card_1 .btn_view:hover {
    background-color: var(--btn_hover);
}


.card_small {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;

}

.cards .card_2 {
    display: flex;
    padding: 30px;
    width: 49%;
    border: 1px solid var(--primary_color);
    border-radius: 30px 0 30px 0;
    background: var(--bg_cards_color);
    transition: .5s;
}
.cards .card_2:hover{
    background: var(--card_hover);

}
.cards .card_2 .content_card_2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.cards .card_2 .content_card_2 .title {
    height: auto;
    display: flex;
    align-items: center;
}

.cards .card_2 .content_card_2 .title svg {
    font-size: 50px;
    margin-right: 20px;
}

.cards .card_2 .content_card_2 .title h2 {
    color: var(--black);
    font-size: 3rem;
}

.cards .card_2 .content_card_2 h3 {
    font-weight: 300;
    font-size: 1.5rem;
    padding: 12px 0;
    color: var(--black);
}

.cards .card_2 .content_card_2 p {
    font-weight: 100;
    color: var(--black);
}

.cards .card_2 .content_card_2 .btn_view {
    width: 140px;
    margin-top: 50px;
    padding: 10px 25px;
    color: var(--black);
    text-decoration: none;
    text-align: center;
    transition: .3s;
}

.cards .card_2 .content_card_2 .btn_view:hover {
    background-color: var(--btn_hover);
}

@keyframes show {
    from {
        opacity: 0;
        scale: 25%;
    }

    to {
        opacity: 1;
        scale: 100%;
    }
}

.cards .card_1,
.cards .card_2 {
    view-timeline-name: --image;
    view-timeline-axis: block;

    animation-timeline: --image;
    animation-name: show;

    animation-range: entry 0 cover 30%;
    animation-fill-mode: both;
}