.mainNumberSection{
    position: relative;
    height: 100vh;
    width: 100%;
    background: var(--primary_color);
    color: var(--white);
}
.mainNumberSection .content{
    display: flex;
    align-items: center;
    flex-direction: column;
    }
.mainNumberSection .content span .icon{
    margin-top: 100px;
    font-size: 4rem;
    animation: moveUpDown 2s ease-in-out infinite;
}
.mainNumberSection .content h3{
    width: 50%;
    margin-top: 30px;
    font-size: 4.2rem;
    font-weight: 700;
    letter-spacing: -3px;
    text-align: center;
}
.mainNumberSection .content .box{
    margin-top: 50px;
    display: flex;
    text-align: center;
    justify-content: space-between;
    max-width: 1000px;
    width: 70%;
    
}
.mainNumberSection .content .box .box_content{
    width: 48%;
    padding: 10px;
    border-top: 2px solid var(--gray_txt);
  
}
.mainNumberSection .content .box .box_content h2{
    font-size: 5.2rem;
}
.mainNumberSection .content .box .box_content p{
    font-size: 1.8rem;
    font-weight: 300;
}
@keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-20px); /* Animación de desplazamiento hacia abajo */
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }