body {
    --primary_color: #00bfff;
    --light_blue: #81dfff;
    --btn_hover: #00384b;
    --card_hover: #00bfff3d;
    --white: #f1f1f1;
    --black: #161616;
    --gray_card: #f0f0f0;
    --transparent: #00000000;
    --solid_black: #161616;
    --solid_white: #e0e0e0;
    --gray_border_solid: #c5c5c5;
    --border_radius: 10px;
    --filter: invert(0);
    --padding_btn: 12px 25px;
    --gray_txt: #222222;
    --bg_cards_color: #d8d8d8;
}

@media (prefers-color-scheme: dark) {
    body {
        --primary_color: #00B1ED;
        --light_blue: #81dfff;
        --white: #161616;
        --black: #f1f1f1;
        --gray_card: #1A1A1A;
        --solid_black: #161616;
        --solid_white: #e0e0e0;
        --gray_border_solid: #1A1A1A;
        --filter: invert(100%);
        --gray_txt: #222222;
        --bg_cards_color: #1d1d1d;
        --card_hover: #00384b36;


    }

}

.gradient_buttons {
    border: 1px solid transparent;
    border-image: linear-gradient(90deg, rgba(0, 177, 237, 1) 0%, rgba(0, 177, 237, 0.13209033613445376) 100%);
    border-image-slice: 1;
}
.gradient_buttons_invert{
    border: 1px solid transparent;
    border-image: linear-gradient(90deg, rgba(0, 177, 237, 0) 0%, rgba(0, 177, 237, 1) 100%);
    border-image-slice: 1;
}
.box_shadow{
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}