
.nav ul li {
  list-style: none;
}
.nav ul li a {
  text-decoration: none;
  color: var(--black);
  transition: .5s;
}
.nav ul li a:hover{
  color: var(--primary_color);
}

.nav {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: 8vh;
  background: var(--white);
  z-index: 1000;
  border: 1px solid var(--gray_border_solid);
}
.logo {
  width: auto;
  height: 70%;
}

.logo img {
  height: 100%;
  width: auto;
}

.nav__menu {
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: 3rem;
}
.nav__toggler {
  display: none;
}
.nav__toggler div {
  width: 2.5rem;
  height: 0.2rem;
  margin: 0.4rem;
  background: rgb(204, 204, 204);
  transition: 0.3s ease-in;
}

@media screen and (max-width: 768px) {
  .nav__toggler {
    display: block;
    cursor: pointer;
  }
  .nav__menu {
    position: fixed;
    top: 7vh;
    right: 0;
    height: 93vh;
    width: 50vw;
    background: var(--white);
    flex-direction: column;
    transform: translateX(100%);
    transition: 0.5s ease-in;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  }
}
.nav__active {
  transform: translateX(0%);
}

.toggle .line1 {
  transform: rotate(-45deg) translate(-4px, 5px);
}
.toggle .line2 {
  opacity: 0;
}
.toggle .line3 {
  transform: rotate(45deg) translate(-4px, -5px);
}