.popup-container {
  position: relative;
}

.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Fondo semi-transparente */
  backdrop-filter: blur(5px);
  display: flex;
  justify-content: center;
  z-index: 1000;
}

.popup-content {
  max-width: 600px;
  height: auto;
  padding: 50px;
  background-color: var(--gray_card);
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  position: absolute;
  top: 100px;
  z-index: 999;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transform: translateY(-50px); /* Mueve el popup hacia arriba */
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.popup-content.show {
  opacity: 1;
  transform: translateY(0); /* Vuelve a la posición original */
}

.confetti-container {
  width: 600px;
  height: 100%;
  pointer-events: none;
  z-index: 1000;
}

.close-btn{
  position: absolute;
  height: auto;
  top: 15px;
  right: 15px;
  background: transparent;
  border: none;
  cursor: pointer;
  font-size: 16px;
}

.popup-content h1 {
  color: var(--primary_color);
  margin-bottom: 30px;
}

.popup-content p {
  color: var(--black);
  text-align: center;
  line-height: 1.5;
  font-size: 1.2rem;
  font-weight: 200;
  margin: 0;
}

.img_anniver{
    width: 200px;
}

.close-icon{
  color: var(--black);
  transition: color 0.3s ease;
}

.close-icon:hover {
  color: var(--primary_color); /* Cambia el color a rojo al hacer hover */
}
