.main {
  position: relative;
  height: 85vh;
  width: 100%;
  background-color: var(--solid_black);
  z-index: 0;
}

.img_bg img {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: auto;
  z-index: -10; 
  opacity: 30%;
} 

.box_header{
  position: absolute; 
  top: 230px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content:center;

}
.header_content {
  max-width: 1000px;
  opacity: 0; 
  animation: fadeIn 1s forwards; 
  margin-left: 250px;
}

.slogan {
  font-size: 2rem;
  font-weight: 500;
  color: var(--solid_white);
}
.title {
  font-size: 6rem;
  color: var(--primary_color);
  font-weight: 800;
  letter-spacing: -1px;
}
.title span {
  color: var(--light_blue);
  
}

.phrase{
  margin-bottom: 40px;
  font-size: 2rem;
  color: var(--solid_white);
  font-weight: 300;
}

.btn_services {
  padding: var(--padding_btn);
  color: var(--solid_black);
  background: var(--primary_color);
  text-decoration: none;
  transition: .3s;
}

.btn_services:hover {
  background-color: var(--btn_hover);
  color: var(--solid_white);
}

.arrow_down {
  margin-top: 30px;
  color: var(--black);
  animation: moveUpDown 2s ease-in-out infinite;
}
